@import "var";

* {
  box-sizing: border-box;
}

body {
  background-color: $light-white;
  height: 100%;
  min-width: 320px;
  max-width: 100%;
}

html {
  min-height: 100%;
}

h1, h2, h3, h4, h5, h6, span, p, small, strong {
  margin: 0;
}

ul {
  padding: 0;

  li {
    list-style-type: none;
  }
}